import { CategoriaDTO } from 'models/domain/categoria.dto';
import { CategoriaService } from './../categoria.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractBaseComponent } from 'config/core/abstract-base.component';



@Component({
  selector: 'categoria-popup-modal',
  templateUrl: './categoria-popup-modal.component.html',
  styleUrls: ['./categoria-popup-modal.component.scss']
})
export class CategoriaPopupModalComponent extends AbstractBaseComponent implements OnInit {

  titulo: string = "";
  categoria: CategoriaDTO;

  @ViewChild('modalConfirm') confirma;
  @ViewChild('campoNome') campo;

  @Output() onClickConfirm = new EventEmitter<any>();

  constructor(
    public fb: FormBuilder) {
    super();
  }

  ngOnInit() {
    this.criarForm();
  }

  show() {
    this.titulo = "Cadastra categoria"
    this.criarForm();
    this.confirma.show();
    this.fieldFocus(this.campo);
  }

  showAlteracao(item: CategoriaDTO) {
    this.titulo = "Alterar categoria"
    this.alterarForm(item)
    this.confirma.show();
    this.fieldFocus(this.campo);
  }

  btnConfirmar(categoria: CategoriaDTO) {
    categoria.urlImagem = this.urlFoto
    this.onClickConfirm.emit(categoria);
    this.confirma.hide();
  }

  mudarImage(evento) {
    this.atualizarFoto(evento.foto);
  }

  private alterarForm(categoria: CategoriaDTO) {
    this.formulario = this.fb.group({
      id: [categoria.id],
      nome: [categoria.nome, [Validators.required, Validators.minLength(5), Validators.maxLength(25)]],
      status: [categoria.status],
      comentario: [categoria.comentario],
      urlImagem: [categoria.urlImagem],
      emAlteracao: [true]
    });
    this.urlFoto = categoria.urlImagem;
  }

  private criarForm() {
    this.formulario = this.fb.group({
      id: [],
      nome: ['', Validators.required],
      status: ['A'],
      comentario: [''],
      foto: [''],
      emAlteracao: [false]
    })
    this.urlFoto = this.urlFotoAvatar;
  }

  private atualizarFoto(foto: any) {
    this.urlFoto = foto;
  }
}



